<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-gongsixiangqinglogo"></i>
				<span>新增自管基金</span>
			</div>
		</div>
		<div class="form flex_between">
			<div class="form_list flex_start">
				<div class="form_list_left">
					合伙企业
				</div>
				<div class="form_list_right">
					<el-select v-model="fundName" :remote-method="remoteMethod" @change="changeName" :loading="loading" filterable remote :clearable='true' reserve-keyword placeholder="请输入公司名称">
						<el-option v-for="item in tianYanBackData" :key="item.name" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金期限（年）
				</div>
				<div class="form_list_right fund_limit_time flex_between">
					<el-input v-model="duration[0]" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="duration[1]" placeholder="" clearable>
					</el-input>
					<span>+</span>
					<el-input v-model="duration[2]" placeholder="" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金简称
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="simpleName" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					协议到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="protocol_end_date" :editable='false' type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd">
    					</el-date-picker>
				</div>
			</div>
			<!--<div class="form_list flex_start">
				<div class="form_list_left">
					基金到期日
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="end_date" type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd">
    				</el-date-picker>
				</div>
			</div>-->
			<div class="form_list flex_start">
				<div class="form_list_left">
					普通合伙人
				</div>
				<div class="form_list_right">
					<el-select v-model="investor_id" clearable  filterable placeholder="请选择">
					    <el-option v-for="item in all_investor" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金类型
				</div>
				<div class="form_list_right">
					<el-select v-model="is_master" clearable placeholder="请选择">
					    <el-option v-for="item in fund_type" :key="item.value" :label="item.label" :value="item.value">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start" v-if="is_master===0">
				<div class="form_list_left">
					主体自管基金名称
				</div>
				<div class="form_list_right">
					<el-select v-model="p_petfund_id" clearable placeholder="请选择" filterable>
					    <el-option v-for="item in masterMotherFundList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			
			<div class="form_list flex_start">
				<div class="form_list_left">
					备案状态
				</div>
				<div class="form_list_right">
					<el-select v-model="record_state" clearable placeholder="请选择">
					    <el-option v-for="item in all_record_state" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					管理公司
				</div>
				<div class="form_list_right">
					<el-select v-model="sg_company_manager_id" clearable filterable placeholder="请选择">
					    <el-option v-for="item in all_sg_company_manager_list" :key="item.id" :label="item.company_name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					备案编码
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="record_state_number" clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					币种
				</div>
				<div class="form_list_right">
					<el-select v-model="unit" clearable placeholder="请选择">
					    <el-option v-for="item in coinList" :key="item.value" :label="item.label" :value="item.value">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					数据创建人
				</div>
				<div class="form_list_right">
					<el-select v-model="create_user_id" clearable filterable placeholder="请选择">
					    <el-option v-for="item in allUserList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					工商设立日期
				</div>
				<div class="form_list_right">
					<el-date-picker v-model="fund_date"  :editable='false' type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd" @change="toCalcYear()">
    					</el-date-picker>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					基金状态
				</div>
				<div class="form_list_right">
					<el-select v-model="fund_state" clearable placeholder="请选择">
					    <el-option v-for="item in all_fund_state" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					ODI额度(美元/万元）
				</div>
				<div class="form_list_right">
					<el-input placeholder="请输入内容" v-model="odi"  clearable>
					</el-input>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					募集状态
				</div>
				<div class="form_list_right">
					<el-select v-model="collect_state" clearable placeholder="请选择">
					    <el-option v-for="item in all_collect_state" :key="item.id" :label="item.value" :value="item.id">
					    </el-option>
					</el-select>
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					是否隐藏
				</div>
				<div class="form_list_right flex_start">
					<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[is_hide?'radio_btn_active':'']">
						是
					</div>
					<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!is_hide?'radio_btn_active':'']">
						否
					</div>
				</div>
			</div>
			<div class="form_list flex_start" v-if="is_master === 0">
				<div class="form_list_left">
				</div>
				<div class="form_list_right flex_start">
				</div>
			</div>
			<div class="form_list flex_start">
				<div class="form_list_left">
					历史倍数
				</div>
				<div class="">
					<div class="histor_multiple_box table " style="max-width:805px">
						<el-table  :data="tableData" border  >
							<el-table-column v-for="(tableHeaderitem,index) of tableHeader " :key="index" :prop="index.toString()" :label="tableHeaderitem" width='120'>
								<template slot-scope="scope">
									<el-input  v-model="scope.row[index]" clearable @blur="historyCheck(scope.row[index],index)">
									</el-input>
								</template>
							</el-table-column>
							<el-table-column fixed="left" label="年份" width="70">
								<template slot-scope="scope">
									<span>倍数</span>
								</template>
							</el-table-column>
						</el-table>
					</div>
				</div>
			</div>
		</div>
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancel">取消</button>
			<button @click="saveSelfFundInfo">保存</button>
		</div>
	</div>
</template>

<script>
	import { getSelfAddInitApi,getUserlistApi,getTianyanIdApi,saveSelfFundInfoApi } from '../../config/api'
	import { getMasterPetfund} from '../../config/apiSj'
	
	import { dialogConfirm, dialogConfirmHasbtn,isValidNumber ,isValidDecimal} from '../../config/mUtils'
	
	export default {
		name: 'selfAdministerFundAdd',
		data() {
			return {
				loading: false,
				tianYanBackData: [],
				
				all_record_state: [],//备案状态
				all_fund_state: [],//基金状态
				all_collect_state: [],//募集状态
				all_sg_company_manager_list: [],//管理公司
				all_investor: [],//所有投资人
				allUserList: [],//公司所有员工
				// 货币类型
				coinList: [
					{
						value: 1,
						label: '人民币'
					},
					{
						value: 2,
						label: '美元'
					}
				],
				fund_type: [
					{
						label: '主体自管基金',
						value: 1
					},
					{
						label: '分支自管基金',
						value: 0
					},
				],
				// 表单数据开始
				fundName: '',
				simpleName: '',
				is_master: '',
				p_petfund_id:'',
				investor_id: '',
				sg_company_manager_id: '',
				unit: 1,
				fund_date: '',
				odi: '',
				duration: ['','',''],
				protocol_end_date: '',
//				end_date:'',
				record_state: '',
				record_state_number: '',
				create_user_id: '',
				fund_state: '',
				collect_state: '',
				is_hide: 0,
				tianyan_id: '',
				// 表单数据结束
				
				masterMotherFundList:[],
				
				company: '',
				data: '',
				options: [
					{
			          value: '选项1',
			          label: '黄金糕'
					}, 
					{
			          value: '选项2',
			          label: '双皮奶'
			         }
				],
				value4: '',

				//表头数据
				tableHeader:[],
				//表格数据
				tableData: [{}],

				history:[],
			}
		},
		created() {
			this.getSelfAddInit();
			this.getAllUserList();
			this.toGetParentFof();
		},
		mounted() {},
		components: {},
		methods: {
			historyCheck(multiple,index){
				if(Number(multiple)!=multiple || multiple<0){
					dialogConfirm('info','历史倍数格式错误或小于0');
					this.tableData[0][index] = 0;
				}
				if(this.tableData[0][index]==''){
					this.tableData[0][index] = 0;
				}
			},
			getSelfAddInit(){
				getSelfAddInitApi({

				}).then(res => {
					// //console.log(res);
					if(res.data.msgNo == 1) {
						this.all_record_state = res.data.payload.all_record_state;
						this.all_fund_state = res.data.payload.all_fund_state;
						this.all_collect_state = res.data.payload.all_collect_state;
						this.all_sg_company_manager_list = res.data.payload.all_sg_company_manager_list;
						this.all_investor = res.data.payload.all_investor;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			saveSelfFundInfo(){
				// 判断odi额度是否大于0
				// if (parseFloat(this.odi) < 0 || (!parseFloat(this.odi))) {
				// 	dialogConfirm('info', 'ODI额度应不小于0');
				// 	return
				// }
				if(this.odi!=''){
					if(!isValidDecimal(this.odi)||parseFloat(this.odi) < 0){
						dialogConfirm('info', 'ODI额度应大于等于0');
						this.odi = '';
						return
					}
				}
				//验证合伙企业、基金简称、管理公司比填
				if(this.fundName&&this.simpleName&&this.sg_company_manager_id){
					//处理 基金期限（年）的数据格式
					var is_valid_number = this.isValidFundTime();
					if (is_valid_number) {
						var duration = JSON.parse(JSON.stringify(this.duration));
						if(duration[1]==''&&duration[2]==''){
							duration = duration[0];
						}else if(duration[1]==''&&duration[2]!=''){
							dialogConfirm("info", '基金期限不能为空!');
							return;
						}else if(duration[1]!=''&&duration[2]==''){
							duration.splice(2,1);
							duration = duration.join('+');
						}else{
							duration = duration.join('+');
						}
					} else {
						return
					}
					//console.log(duration);
					if(this.is_master){
						this.p_petfund_id = 0;
					}

					let params = {
						fundName: this.fundName,
						simpleName: this.simpleName,
						is_master: this.is_master,
						p_petfund_id:this.p_petfund_id,
						investor_id: this.investor_id,
						sg_company_manager_id: this.sg_company_manager_id,
						unit: this.unit,
						fund_date: this.fund_date,
						odi: this.odi,
						duration: duration,
						protocol_end_date: this.protocol_end_date,
						record_state: this.record_state,
						record_state_number: this.record_state_number,
						create_user_id: this.create_user_id,
						fund_state: this.fund_state,
						collect_state: this.collect_state,
						is_hide: this.is_hide,
						tianyan_id: this.tianyan_id,
					};

					//历史倍数
					let historyArr = [];
					this.tableHeader.forEach((element,index) => {
						let newObj = {
							year:element,
							multiple:this.tableData[0][index]?this.tableData[0][index]:'0',
						};
						historyArr.push(newObj);
					});
					if(historyArr.length){
						params.history = JSON.stringify(historyArr);
					}else{
						params.history = '';
					}
					saveSelfFundInfoApi(params).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.$router.go(-1)
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}else{
						
					if (!this.fundName) {
						dialogConfirm("info", '合伙企业不能为空');
					}else if(!this.simpleName){
						dialogConfirm("info", '基金简称不能为空');
					}else if(!this.sg_company_manager_id){
						dialogConfirm("info", '管理公司不能为空');
					}
				}
					

				
			},
			// 判断基金期限是否有效
			isValidFundTime(){
				if (this.duration[0]) {
					let is_valid_fund_expire1 = isValidNumber(this.duration[0]);
					let is_valid_fund_expire2 = isValidNumber(this.duration[1]);
					let is_valid_fund_expire3 = isValidNumber(this.duration[2]);
					if(this.duration[1]&&!is_valid_fund_expire2){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if(this.duration[2]&&!is_valid_fund_expire3){
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
					if (is_valid_fund_expire1) {
						return true
					} else{
						dialogConfirm("info", '基金期限输入格式不对');
						return false
					}
				} else{
					dialogConfirm("info", '基金期限不能为空');
					return false
				}
			},
			// 获取所有人员列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.allUserList = res.data.payload.data;
						this.create_user_id = parseInt( window.localStorage.getItem('htcx_uid')) ;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			changeName(e){
				// //console.log(e);
				this.getTianYanId()
			},
			getTianYanId(){
				var data = this.tianYanBackData;
				for (var i = 0,max = data.length;i<max;i++) {
					if (data[i].name == this.fundName) {
						if (data[i].id) {
							this.tianyan_id = data[i].id;
						} else{
							this.tianyan_id = '';
						}
						return
					}
				}
			},
			// 获取天眼查返回的数据
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getTianyanIdApi({
						name: query
					}).then(res => {
						this.loading = false; 
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.tianYanBackData = [{
									name: query
								}]
							} else {
								let newObj = {
									name: query
								}
								res.data.payload.unshift(newObj);
								this.tianYanBackData = res.data.payload;
							}

						}
					})
				} else {
					this.tianYanBackData = [];
				}
			},
			changeHideStatus(e){
				if (e == 1) {
					this.is_hide = 1;
				} else{
					this.is_hide = 0;
				}
			},
			cancel(){
				this.$router.go(-1)
			},

			//获取所有的主体自管基金
			toGetParentFof(){
				getMasterPetfund().then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data;
						////console.log(res);
						this.masterMotherFundList = data;
					}
				})
			},

			//计算出来对应的历史倍数年限
			toCalcYear(){
				this.tableHeader = [];
				this.tableData = [{}];
				if(this.fund_date&&this.protocol_end_date){
					//console.log(222);
					let  fund_date = Date.parse(this.fund_date);//工商设置日期
					let  end_date = Date.parse(this.protocol_end_date);//基金到期日
					if(fund_date <= end_date){
						let fund_year = this.fund_date.split('-')[0];
						let end_year = this.protocol_end_date.split('-')[0];
						let number = parseInt(end_year) - parseInt(fund_year) ;
						for(let i = 0;i<=number;i++){
							this.tableHeader.push((parseInt(fund_year)+i).toString());
						}
					}else{
						this.fund_date = '';
						dialogConfirm('info','工商设立日期不能大于基金到期日');
					}
				}
				
				if(this.tableHeader.length){
					let tableDataObj = [{}];
					this.tableHeader.forEach((element,index) => {
						tableDataObj[0][index] = 0
						
					});
					this.tableData=tableDataObj;
				}
				
			}
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form{
		flex-wrap: wrap;
		padding: 30px 0;
		color: #6F7071;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 19px;
	}
	.form_list_left{
		width: 120px;
		color: #6F7071;
	}
	.form_list_right{
		width: 270px;
    	height: 40px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
</style>


